
*,::after,::before{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


body{
    font-family: 'Inter', sans-serif;
    font-size: 14px;
    min-height: 100vh;
}
.container{
    width: 100vw;
    height: calc(100vh - 60px);
    display: grid;
    place-content: center;
   
    
}


